import z from 'zod';

import GraphOptionsObject from './graph-options';
import { BAR_COLORS } from '../constants/colors';

// export type PolarAreaChartSubjectOverviewOptions = GraphOptions & {
//   backgroundColors?: string[];
//   graphColorScale?: string;
// };

const PolarAreaChartSubjectOverviewOptionsObject = z.intersection(
  GraphOptionsObject,
  z.object({
    backgroundColors: z.string().array().optional().default(BAR_COLORS),
    graphColorScale: z.string().optional(),
  }),
);

export default PolarAreaChartSubjectOverviewOptionsObject;

/**
 * The options for the polar chart subject overview.
 *
 * @property {string[]} backgroundColors - The background colors of the pie chart. Default: [
 *                                       '#FF6384', '#4BC0C0', '#FFCD56', '#C9CBCF', '#36A2EB',
 *                                       '#B284BE'
 *                                       ]
 * @property {string} graphColorScale - The color scale string for the graph.
 */
export type PolarAreaChartSubjectOverviewOptions = z
  .infer<typeof PolarAreaChartSubjectOverviewOptionsObject>;
